@import url("https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap");

:root {
  --primary: #efb6b2;
  --secondary: #4f4f4f;
  --background: #ffffff;
  --error: #ff4a4a;
}

/* base styles & header */
body {
  font-family: "Noto Serif", sans-serif;
  color: var(--secondary);
  background-color: var(--background);
}

.App {
  max-width: 960px;
  margin: 0 auto;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}

header h2,
header p {
  text-align: center;
}

header h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}

.checkbox {
  opacity: 0;
  position: absolute;
}

.toggler {
  background-color: #111;
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  height: 6px;
  transform: scale(1.5);
}

.toggler .ball {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 3px;
  left: 3px;
  height: 10px;
  width: 10px;
  transform: translateX(0px);
  transition: transform 0.2s linear;
}

.checkbox:checked + .toggler .ball {
  transform: translateX(10px);
}

/* upload form styles */
form {
  margin: 30px auto 10px;
  text-align: center;
}

.uploadFormLabel input {
  height: 0;
  width: 0;
  opacity: 0;
}

.uploadFormLabel {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
  transition: all 0.2s ease-in;
}

.uploadFormLabel:hover {
  background: var(--primary);
  color: white;
  cursor: pointer;
}

.output {
  height: 60px;
  font-size: 0.8rem;
}

.error {
  color: var(--error);
}

/* progress bar styles */
.progress-bar {
  height: 5px;
  background: var(--primary);
  margin-top: 20px;
}

/* image grid styles */
.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 40px;
}

.img-wrap {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
  opacity: 0.8;
  cursor: pointer;
}

.img-wrap img {
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}

/* modal styles */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 1px solid white;
}

/* media query styles */
@media screen and (max-width: 580px) {
  .img-grid {
    width: 70%;
  }

  .backdrop {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .backdrop img {
    max-width: 90%;
    margin: auto;
  }
}
